import React, { useMemo } from "react";
import { H1, H2 } from "../../components/Header";
import { Link as GatsyLink } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";

import "./styles.scss";

const HomeCard = (props) => {
  const { category, title, photos, coverPhoto } = props;

  const coverPhotoFile = useMemo(() => {
    return photos.find(photo => {
      return photo.description === coverPhoto;
    });
  }, [photos]);

  return (
    <div className="CategoryLinkContainer">
      <GatsyLink className="CategoryLink" to={`/category/${category}`}>
        <H2>{title}</H2>
        <GatsbyImage
          image={getImage(coverPhotoFile.file)}
          alt={title}
        />
      </GatsyLink>
    </div>
  );
}

const Home = (props) => {
  const { categories, photos } = props;

  return (
    <div className="PetitsAnimauxSection">

      <H1>Shop Top Categories</H1>

      <div className="CategoryContainer">
        {categories.filter(category => Boolean(category.active)).map((category) => {
          return (
            <HomeCard key={category.category} {...category} photos={photos} />
          );
        })}
      </div>
    </div>
  )
};

export default Home;